

#trending_root_element{

    display: flex;
    justify-content: space-between;
    flex-wrap:wrap;
    height: fit-content;
    transition-duration: 1500ms;
   background-image:url('./trending.jpg');
   background-position: center;
   background-size: cover;
   overflow:hidden;
   margin-top: -10px;
 
}

#container_for_button_and_gallery{
       width: 55%;
       height: fit-content;
     	margin-top:20px;
     	margin-bottom:20px;
	    backdrop-filter: 50px;
       transition-duration: 1500ms;
	   border-radius:50px;
       /* background-color: #02345d6f; */
       backdrop-filter:blur(5px);     
	   
}

#trending_gallery {
    
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    border-radius: 20px;
    width: 100%;
    height: fit-content;
	animation-duration:1000ms;
}
#trending_button_section {

     width: 100%;
     display: flex;
	 border-radius:15px;
     height: fit-content;
     justify-content: space-evenly;
     margin-top: 30px;
     margin-bottom: 2px;
	 background-color :#ffffff6b;
	/* background: linear-gradient(50deg, #133c59, #4a7a97, #ffd98f, #ffb60c);*/
	 backdrop-filter:blur(5px);

 	 
   
	 

}
 
#trending_text {

  animation-duration:1500ms;
   font-size:120%;
}
 
 

#beach_trending,
#city_trending,
#nature_trending {

    font-size: 150%;
     padding:5px 10px;
	margin:5px 0px;
    cursor: pointer;
    transition-duration: 600ms;
    border-bottom: 3px solid transparent;
	font-weight:600;
}

/*selected as default*/
#beach_trending {

    border-bottom-color: #f4b41a;
}

 
#beach_trending:hover,
#city_trending:hover,
#nature_trending:hover {

    color: white;
	 text-shadow: 0px 0px 10px white;
   
 
}

 
.trending_photo_card   
{
    cursor: pointer;
    width:  250px;
    height: 270px;
    margin: 10px;
    backdrop-filter: blur(26px);
    border-radius: 15px;
     
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    box-shadow: 0px 0px 25px 1px black;
   text-shadow: 0px 0px 3px  white;
    display: flex;
    justify-content: end;
    flex-direction: column;
 
}

.trending_photo_card:hover{
    border:1px double #111d2e;
    transform: perspective(5000px) translate3d(0, 0, 100px);
    transition-duration: 0.3s;
 
  
 
}

 

.trending_text_content {
    display: flex;
    justify-content: space-evenly;
    font-size: 15px;
    box-shadow: 0px 0px 5px black;
 
    border-radius: 5px;
	color:white;
    /* background-color: red; */
   backdrop-filter: blur(15px);
   background-color: rgba(0, 0, 0, 0.19);
  
 
}

.trending_name,
.trending_price,
.trending_rate,
.trending_visitors {
    
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-top: 5px;
}
 
#trending_some_text{

 color:white;
 font-size:300%;
 width:38%;
 font-style:italic;
  margin-left:5px;
  text-align:center;
  display:flex;
  justify-content:center;
  flex-direction:column;
  text-shadow: 2px 2px 10px white;
    
} 

 
#trending_text_second_line,
#trending_text_first_line{
	  animation-duration:1500ms;
	   
	  	 
}

@keyframes first_line {
    from {
		opacity:0;
		transform:translateY( 20px);
	}
	to{
    	opacity:1;
	}
}


@keyframes second_line {
    from {
		opacity:0;
		transform:translateY(-20px);
	}
	to{
    	opacity:1;
	}
}
 
 