 

#hotel_root {
width:100%;
height:100%;
 background-color:#ddd;
 border-radius:15px;
}

#hotel_name_text {
 width:100%;
 font-size:250%;
 text-align:center;
}

#hotel_photos_container
{
  background-color:black;
  width:100%;
  height:400px; 
}

#upper_photo_hotel {

width:100%;
height:50%;
	background-position:center;
	background-size:cover;
	cursor:pointer;
	border:2px solid black;

}
#lower_photos_hotel
{
	
width:100%;
height:50%;
display:flex;
	
}
#lower_1_hotel ,#lower_2_hotel
{
	width:50%;
    height:100%;
	background-position:center;
	background-size:cover;
	cursor:pointer;
	border:2px solid black;
}

#upper_photo_hotel:hover , #lower_1_hotel:hover ,#lower_2_hotel:hover{
    transform: perspective(5000px) translate3d(0, 0, 100px);
    transition-duration: 0.3s;
    border-radius: 10px;
 
}

#hotel_gallery_box
{
	width: 70%;
	height:30%;
 background-position:center;
 background-size:cover;
 position:absolute;
 z-index:101;
  left: 15%;
}
#hotel_info_header{
 font-size:200%;
 display:flex;
 justify-content:space-around;
}
#hotel_features{
	display:flex;
	justify-content:space-around;
	flex-wrap:wrap;
} 
#hotel_features div
{
	margin:15px;
}