.Header_Header {
   position: absolute;
   display: flex;
   justify-content: space-between;
   width: 100%;
   z-index:1;
 
 
}

.Header_Home-About,
.Header_Login-Register {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width : fit-content;
    
    font-weight: bold;
}
 
 
.Header_Button-Home,
.Header_Button-About,
.Header_Button-Register,
.Header_Button-Login,
.Header_Button-Logout {
    text-decoration: none;
    font-size: 150%;
    padding: 20px;
    color: #365486;
    border-bottom: transparent 2px solid;
    transition-duration: 800ms;
    
}
.Header_Button-About:focus,
.Header_Button-Home:focus,
.Header_Button-Login:focus,
.Header_Button-Register:focus,
.Header_Button-Logout:focus{
    border-bottom: 2px solid #111d2e;
}

.Header_Button-Home:hover,
.Header_Button-About:hover,
.Header_Button-Register:hover,
.Header_Button-Login:hover,
.Header_Button-Logout:hover{
background-color: rgb(173, 173, 173);
border-radius: 15px;
text-shadow:0px 0px 10px #444;

}
