[class^="rev_photo_description"] {
  width:80%;
  height: 67%;
  border-radius: 5px;
  margin: 5% auto;
  transition-duration: 0.8s;
  backface-visibility: visible;
  align-items: center;
  display: flex;
  justify-content: center;

  transform-style: preserve-3d;
}

[class^="photo_description"] {
  width: 100%;
  height: 100%;

  text-align: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 5%;
  backdrop-filter: blur(26px);
  transform-style: preserve-3d;
  transform: rotateY(180deg);
  backface-visibility: hidden;
  text-shadow: 0px 0px 5px white;
  color: white;
  background-color: transparent;
  border-radius: 10px;
}
[class^="more_info"] {
  background-color: transparent;
  padding: 2%;
  margin-left: 2%;
  color: white;
  cursor: pointer;
  font-weight: bold;
  box-shadow: 0px 0px 8px white;
  text-shadow: 0px 0px 5px rgb(93, 86, 86);
  border-radius: 10px;
  font-size: 120%;
  width: fit-content;
}
[class^="more_info"]:hover {
  background-color: rgba(112, 112, 112, 0.419);
}

[class^="photo_card"] {
  cursor: pointer;
  width: 20%;
  height: 380px;
  backdrop-filter: blur(1px);
  /* padding: 10px; */
  margin: 2%;
  border-radius: 15px;
  transition-duration: 400ms;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  box-shadow: 0px 0px 25px 1px black;
}

[class^="photo_card"]:hover {
  box-shadow: 0px 0px 5px 1px white;
}
.container_for_text_and_button {
  box-shadow: 0px 0px 5px black;
  background-color: rgba(88, 88, 88, 0.621);
 border-radius:10px;
  z-index: -5;
  width:100%;
  position: absolute;
  bottom: 2px;
}
.photo_text_content {
  display: flex;
  justify-content: space-evenly;
  color: #fff;
  font-weight: 800;

   
}

[class^="photo_card"]:hover .photo_text_content {
  text-shadow: 0px 0px 5px white;
}
[class^="photo_card"]:hover .photo_price i {
  text-shadow: 0px 0px 5px rgb(23, 221, 23);
}
[class^="photo_card"]:hover .photo_rate i {
  text-shadow: 0px 0px 5px yellow;
}
[class^="photo_card"]:hover .photo_name i {
  text-shadow: 0px 0px 5px red;
}

#photo_no_result {
  background-color: #7f7e7ed1;
  width: 70%;
  height: 20%;
  text-align: center;
  align-items: center;
  display: flex;
  justify-content: center;
  font-size: 50px;
  border-radius:55px;
}

#photo_no_result i {
  padding: 3%;
  color: rgba(0, 0, 0, 0.796);
  
}

