
#profile_edit_background
{
    width: 100%;
    height: 125vh;
    /* max-height: fit-content; */
    background-image: url('./edit_profile_background.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;
}

#profile_edit_root{
    background-color: rgba(246, 246, 246, 0.133);
    backdrop-filter: blur(40px);
    border-radius: 15px;
    width: 50%;
    height: fit-content;
    position: relative;
    top:12%;
    flex-wrap: wrap;
 
}
#profile_update_successful_box{
    padding: 2%;
    font-size: 150%;
    margin-left: 20px;
    z-index: 1;
    background-color: rgba(6, 255, 6, 0.681);
    position: absolute;
    bottom: -50px;
    border-radius: 25px;
    animation-name: profile_info_show;
    animation-duration: 400ms;
   display: none;
}
#profile_edit_root p:first-child{
    text-align: center;
    color: #043a6d;
    text-shadow: 2px 1px #fff;
    font-weight: 500;
    font-size: 2rem;
    margin-top: 3%;
    margin-bottom: 3%;

}
#profile_edit_root p{
    text-align: center;
    margin-bottom: 5%;
}
#profile_edit_form{
    margin: 0px 2%;
    padding-right: 1em;
    align-items: center;
}
 
#profile_edit_form div label{
    font-size: 150%;
    color: #04284b;
    font-weight: bolder;
}
#profile_edit_form div input,
#profile_edit_form div textarea{
    font-size: 120%;
    border:none;
    padding: 1%;
    border-radius: 10px;
    background-color: rgba(248, 248, 248, 0.324);
 
    margin-top: 5px;
    width:60%;
    /* max-width: 30em; */

    
}
#profile_edit_form #button{
    margin-left: 45%;
    background-color: #043a6d;
    color: #fff;
    border-radius: 10px;
    font-size: 120%;
    width: fit-content;
    height:fit-content;
    margin-bottom: 5%;
    margin-top: 3%;
    box-shadow: 2px 2px #978f8f;
    cursor: pointer;
}
#profile_edit_form #button:hover{
    transform: scale(1.1);
}

form div{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 2%;
}
textarea{
    font-size: 15px;
    width: 100%;
    height: 7em;
    margin: auto;
    resize: none;
}
@keyframes profile_info_show {
    from{
        transform: translateY(600px);
        -webkit-transform: translateY(600px);
        -moz-transform: translateY(600px);
        -ms-transform: translateY(600px);
        -o-transform: translateY(600px);
}
}