#home_root{
   width: 100%;
 
   height:fit-content;
 
   position: initial;

 
}
 
/* img_and_title_container */
#img_and_title_container{
    width: 100%;
     
    /* position: relative; */
    transition-duration: 1000ms;
    overflow: hidden;
    position: absolute; 
    display: flex;
    
     /* background-color: red; */
    width: fit-content;
 
    height: fit-content;
    justify-content: center;
    flex-direction: column;
    align-items: center;
     
    top: 10%;
     left: 35%;
   text-shadow: 0px 0px 15px gray;
    
   


}
 
 .home_img_site{
 
    width: 50%;
    height: 100px;
    background-image:url('./title.png');
   
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
 ;
    background-color: rgba(0, 0, 0, 0.2);
    
    border-radius: 50px;
    animation-name: show_up;
    animation-duration: 1.5s;
    animation-iteration-count: 1;

 }
 
 
video {

   
    width: 100%;
    /* position:absolute; */
	object-fit: cover;
    height: 110%;
	margin:0px;
	
}
 .home_title_site{
    font-size: 50px;
    color: gray;
    /* z-index: 100; */
    animation-name: show_up;
    animation-duration: 2s;
 
    animation-iteration-count: 1;
 }
  
 

 
@keyframes show_up {
    from {
     opacity: 0;
     transform: translateY(100px);
    }
    to{
  opacity: 1;
  transform: translateY(0);
    }
}
 
 
#error_box_account_or_0_price{
    position: fixed;
    font-size: 30px;
 
    padding: 25px 100px;
 
    z-index: 225;
    bottom:15px;
    display: none;
    border-radius: 40px;
    /* animation-name: show; */
    animation-duration: 400ms;

  
}
@keyframes show {
    from{
        transform: translateY(600px);
        -webkit-transform: translateY(600px);
        -moz-transform: translateY(600px);
        -ms-transform: translateY(600px);
        -o-transform: translateY(600px);
}
}