
 
#discount_root_element{
     display: flex;
    justify-content: space-around;
     flex-wrap:wrap;
    height: fit-content;
    transition-duration: 1500ms;
	overflow:hidden;
    background-image: url('1.jpg');
    background-position: center;
    background-size: cover;
	 
}
#discount_text_section{
    height: 600px;
    width: 35%;
    font-size: 300%;
    text-align: center;
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-top:40px;
    color: white;
    text-shadow: 1px 1px 15px white;
}
#discount_gallery {
     width: 60%;
    height: fit-content;
    transition-duration: 1500ms;
	 display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    border-radius: 10px;
    margin-top:40px;
 
}

.discount_photo_card {
    width: 30%;
    height: fit-content;
    backdrop-filter: blur(50px);
    border-radius: 15px;
    margin: 2px;
    margin-top: 8px ;
    margin-bottom: 8px ;
    background-color:  rgba(119, 188, 209, 0.146);
 
}

.discount_photo_card:hover{

    border :1px double #111d2e;
}

.discount_photo_img {

    background-position: center;
    background-size: cover;
    width: 100%;
    height: 150px;
    border-radius: 15px;
    cursor: pointer;
}
.discount_photo_img:hover {

    transform: perspective(5000px) translate3d(0, 0, 100px);
    transition-duration: 0.3s;
}

.discount_text_content {

    display: flex;
    justify-content: space-between;
    margin:5px ;
    
}

.discount_name,
.discount_price,
.discount_rate,
.discount_visitors,
.discount_discount {
    
    display: flex;
    align-items: center;
    flex-direction: column;
}
 
 
@keyframes appear_letter {
    
    from {
        opacity: 0;
   
    }
    to{
        opacity: 1;
 
}
}