
#profile_backgorund{
    width: 100%;
     
    height: 150vh;
    background-image: url('./profile_background.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
  
}
#profile_root{
    width: 95%;
    padding: 1% ;
    height: fit-content;
    border-radius: 10px;
    margin: 0px auto ;
    position: relative;
    top: 12%;
    backdrop-filter: blur(15px);
   
 
}

#profile_head{
     
    width: 100%;
    margin: 0  auto ;
    padding: 0 2%;
 
}
#profile_head header{
 
    display: flex;
    justify-content: space-between;
    margin-bottom: 2%;
    align-items: center;
    flex-wrap: wrap;
}
#profile-logo{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    
}
#profile-logo h3{
    background-color: #043a6d;
    border-radius: 50%;
    color: #fff;
    font-weight: 700;
    font-size: 140%;
    width: 40px;
    height: 40px;
    padding-top: 5px;
    margin-right: 5px;
     
    text-align: center;
}
#profile-logo h4{
    font-weight: 500;
    color:  #043a6d;
    font-size: 150%;
}
 
#profile_bio_section{
    color: #000000;
    margin-top: 5% ;
    overflow: auto;
}

#profile-container{
   display: flex;
     justify-content: space-between;
    width: 100%; 

}    
#profile-left{
    width: 30%;
}
#profile_right{
    width: 80%;
    display: flex;
    justify-content: space-between;

}
 
 #edit_my_profile_button{
    font-size: 200%;
    border-radius: 15px;
     background-color: rgba(255, 255, 255, 0.509); 
    padding:1%;
    margin: 1%;
    cursor: pointer;
    text-decoration: none;
    overflow: hidden;
    color:#043a6d ;
}

#edit_my_profile_button:hover {
    background-color:  rgba(255, 255, 255, 0.767); 
    
}
 
#profile_user_photo{
      width: 60%;
      height: 35%;
      background-position: center;
      background-size: cover;
      border-radius: 20px;
      overflow: hidden;
      border: 5px solid #444;
      box-shadow: 5px 7px 25px rgba(0,0,0,0.5);
 
}
#profile_user_photo:hover{
    transform: scale(1.1);
}


#profile_middle_2_part ,
#profile_middle_1_part ,
#profile_middle_3_part
{
    display: flex;
    justify-content: space-around;
    
    flex-wrap: wrap;
    margin: 2% 0px;
}

#profile_middle_2_part div ,
#profile_middle_1_part div ,
#profile_middle_3_part div

{
    background-color: rgba(168, 165, 165, 0.094);
    font-size: 150%;
    padding: 2%;
    font-weight: 600;
    flex-wrap: wrap;
    
} 
#profile_middle_2_part span ,
#profile_middle_1_part span ,
#profile_middle_3_part span{
    color: #043a6d;
    font-size: 100%;
}
 
#profile_bio_section p
{
    font-size: 150%;
    height: 300px;
 
     display: flex;
     flex-wrap: wrap;
     width: 100%;
	 color:white;
    
    border-radius:15px ;
    margin-top: 15px; 
    line-break: anywhere;
    overflow: auto;
	 
    
}
#profile_my_trip_word{
    font-size: 250%;
    text-indent: 1%;
    color: rgb(1, 26, 43);
}
 #profile_trips_done
 {
 
    background-color:  rgba(2, 63, 104, 0.555);
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    
    border-radius: 10px;
    margin-top:20px;
    height: 340px;
    overflow: auto;
    width: 100%;
    text-align: center;
  
}
#profile_trips_done div div{
    color: white;
    text-shadow: 1px 1px 5px white;
}
 
 
.profile_trip_name , .profile_trip_date,
.profile_trip_price, .profile_hotel_name,
.profile_hotel_date{
margin-top: 30px;
  

 
}
 
#profile_root ::-webkit-scrollbar{
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 15px;
  
}
#profile_root ::-webkit-scrollbar-thumb{
    background-color: rgba(255, 255, 255, 0.465);
    border-radius: 15px;
    
 
} 
 
 